<template>
  <section class="nodata-wrapper-container">
    <yd-icon name="warn-outline" color="#C0C4CC" size="2rem"></yd-icon>

    <section class="title-container">{{msg}}</section>
    <yd-button v-if="showBtn" size="large" shape="circle" bgcolor="#4A5B79" color="#fff"
               @click.native="handleClick">{{btnText}}
    </yd-button>
  </section>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'BNodata',
    props: {
      msg: {
        default: '操作失败，请稍候再试或刷新',
        type: String
      },
      btnText: {
        default: '刷新',
        type: String
      },
      showBtn: {
        default: true,
        type: Boolean
      }
    },
    methods: {
      handleClick () {
        this.$emit('btnClick')
      }
    }
  }
</script>

<style type="text/css" scoped>

  /*没有数据样式*/
  .nodata-wrapper-container {
    display: flex;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    flex-direction: column;
    height: 5rem;
    align-items: center;
    justify-content: center;
    color: #C0C4CC;
  }

  .nodata-wrapper-container >>> .yd-btn-block {
    width: 30%;
    height: .7rem;
    line-height: .7rem;
  }

</style>



