<template>
  <section class="toilet-wrapper-container">
    <b-navbar :showLeft="true" title="公厕在这里"></b-navbar>

    <section v-if="!geolocationFail">
      <section id="toilet-container"
               :style="{width:'100%',height:screenHeight+'px'}"></section>

      <yd-popup v-model="showPopup" position="bottom" height="30%">
        <section class="popup-wrapper-container" v-if="toiletDetail">
          <section class="popup-title">
            <section></section>
            <section @click="showPopup = false">
              <yd-icon name="error-outline" size=".4rem" color="#999999"></yd-icon>
            </section>
          </section>
          <section class="popup-content">
            <section class="toilet-container toilet-title">
              <yd-icon name="toilet" custom size=".4rem" color="#4A5B79" class="icon"></yd-icon>
              <section>{{toiletDetail.title}}</section>
            </section>

            <section class="toilet-container address">
              <yd-icon name="location" size=".4rem" color="#4A5B79" class="icon"></yd-icon>
              <section>{{toiletDetail.address}}</section>
            </section>

            <section class="toilet-container distance">
              <yd-icon name="xianlu" custom size=".4rem" color="#4A5B79" class="icon"></yd-icon>
              <section>距您：<span>{{toiletDetail.distance}}</span>米</section>
            </section>
          </section>
        </section>
      </yd-popup>
    </section>
    <section v-else>
      <b-nodata btnText="重新定位" @btnClick="location.reload()"></b-nodata>
    </section>
  </section>
</template>

<script type="text/ecmascript-6">
  import BNavbar from '@/components/basis/BNavbar'
  import BNodata from '@/components/basis/BNodata'
  import BMap from 'BMap'

  export default {
    name: 'Index',
    components: {
      BNavbar,
      BNodata
    },
    methods: {
      initBMap () {
        let _that = this

        let bmap = new BMap.Map(document.getElementById('toilet-container'))
        let geolocation = new BMap.Geolocation()

        let navigation = new BMap.NavigationControl({
          anchor: BMAP_ANCHOR_TOP_LEFT,
          offset: new BMapSub.Size(10, 10)
        })

        bmap.addControl(navigation)

        _that.bmap = bmap

        geolocation.getCurrentPosition(function (ret) {
          if (this.getStatus() === BMAP_STATUS_SUCCESS) {
            let mkIcon = new BMap.Icon('https://aladdin.gosafenet.com/images/red_wz.png', new BMap.Size(32, 32))

            let marker = new BMap.Marker(ret.point, {
              icon: mkIcon
            })

            bmap.addOverlay(marker)
            bmap.panTo(ret.point)
            bmap.centerAndZoom(ret.point, 18)

            _that.point = ret.point
            _that.handleSearchNearbyToilet(ret.point)

          } else {
            _that.geolocationFail = true
          }
        }, {enableHighAccuracy: true, maximumAge: 0})
      },
      handleSearchNearbyToilet (loc) {
        let _that = this
        let point = loc || _that.point

        _that.$dialog.loading.open('搜索中')

        let options = {
          pageCapacity: 20,
          onSearchComplete: function (ret) {
            _that.$dialog.loading.close()

            if (localSearch.getStatus() === BMAP_STATUS_SUCCESS) {
              let temp = []

              // merge ret
              ret.forEach((item) => {
                for (let i = 0; i < item.getCurrentNumPois(); i++) {
                  let poi = item.getPoi(i)

                  temp.push(poi)
                }
              })

              // Array object reduce
              let obj = {}
              temp = temp.reduce((item, next) => {
                obj[next.uid] ? '' : obj[next.uid] = true && item.push(next)

                return item
              }, [])

              temp.forEach((item) => {
                let mkIcon = new BMap.Icon('https://aladdin.gosafenet.com/images/blue_wz.png', new BMap.Size(32, 32))

                let marker = new BMap.Marker(item.point, {
                  icon: mkIcon
                })
                _that.bmap.addOverlay(marker)

                let label = new BMap.Label(item.title, {offset: new BMap.Size(28, -3)})

                label.setStyle({
                  borderColor: '#fff',
                  boxShadow: '.08rem .01rem .2rem #8a8a8a',
                  borderRadius: '50px',
                  padding: '.1rem .3rem .1rem .3rem',
                  whiteSpace: 'nowrap',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis'
                })

                marker.setLabel(label)

                _that.handleMarkerAddClick(item, marker)
              })
            }
          }
        }

        let localSearch = new BMap.LocalSearch(point, options)
        localSearch.searchNearby(['公厕', '厕所', '卫生间', '洗手间'], point, 2000)
      },
      handleMarkerAddClick (content, marker) {
        let _that = this

        marker.addEventListener('click', function (e) {
          _that.handleMarkerClick(content, e)
        })
      },
      handleMarkerClick (content, event) {
        let _that = this
        let p = event.target

        content.distance = _that.bmap.getDistance(_that.point, content.point).toFixed(2)

        _that.toiletDetail = content
        _that.showPopup = true

        // let walking = new BMap.WalkingRoute(_that.bmap, {renderOptions: {map: _that.bmap, autoViewport: true}})
        //
        // walking.search(_that.point, p.getPosition())
      }
    },
    created () {
      this.$nextTick(() => {
        this.initBMap()
      })
    },
    data () {
      return {
        geolocationFail: false,
        toiletDetail: '',
        showPopup: false,
        screenHeight: window.screen.height - 50,
        point: {}
      }
    }
  }
</script>

<style type="text/css" scoped>

  .popup-wrapper-container >>> .popup-content .distance, .popup-content .address {
    color: #909399;
  }

  .popup-wrapper-container >>> .popup-content .distance span {
    color: #F54336;
  }

  .popup-wrapper-container >>> .popup-content .icon {
    margin-right: .2rem;
  }

  .popup-wrapper-container >>> .popup-content .toilet-title {
    font-size: .28rem;
    font-weight: bold;
  }

  .popup-wrapper-container >>> .popup-content .toilet-container:not(:first-child) {
    margin-top: .2rem;
  }

  .popup-wrapper-container >>> .popup-content .toilet-container {
    display: flex;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    align-items: center;
  }

  .toilet-wrapper-container >>> .yd-popup-show {
    border-top-left-radius: .2rem;
    border-top-right-radius: .2rem;
    box-shadow: 0rem -0.03rem 0.1rem #5a5a5a;
    -webkit-box-shadow: 0rem -0.03rem 0.1rem #5a5a5a;
  }

  .toilet-wrapper-container >>> .yd-popup-show .yd-popup-content {
    border-top-left-radius: .2rem;
    border-top-right-radius: .2rem;
    box-shadow: 0rem -0.03rem 0.1rem #5a5a5a;
    -webkit-box-shadow: 0rem -0.03rem 0.1rem #5a5a5a;
  }
</style>



